//objnewobj
function deepCopy(newobj, obj) {
  if (typeof obj != 'object') {
    return obj
  }
  for (var attr in obj) {
    var a = {}
    if (newobj[attr]) {
      a = newobj[attr]
    }
    newobj[attr] = deepCopy(a, obj[attr])
  }
  return newobj
}

