function modelShow(
  title = '',
  content = '?',
  callback = () => {},
  showCancel = true,
  cancelText = '',
  confirmText = ''
) {
  uni.showModal({
    title: title,
    content: content,
    showCancel: showCancel,
    cancelText: cancelText,
    confirmText: confirmText,
    cancelText: cancelText,
    success: function(res) {
      if (res.confirm) {
        // 
        setTimeout(() => {
          callback()
        }, 500)
      } else if (res.cancel) {
        // 
      }
    }
  })
}

//
function timeToDate(date, flag = false) {
  var date = new Date(date * 1000) //date131000
  var Y = date.getFullYear() + '-'
  var M =
    (date.getMonth() + 1 < 10
      ? '0' + (date.getMonth() + 1)
      : date.getMonth() + 1) + '-'
  var D = (date.getDate() < 10 ? '0' + date.getDate() : date.getDate()) + ' '
  var h = (date.getHours() < 10 ? '0' + date.getHours() : date.getHours()) + ':'
  var m =
    (date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes()) + ':'
  var s = date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds()
  if(flag)
  {
	  return Y + M + D
  }
  else
  {
	  return Y + M + D + h + m + s
  }
}

function time2date(micro_second) {
  var time = {}
  // 
  var second = Math.floor(micro_second)
  // 
  time.day = PrefixInteger(Math.floor(second / 3600 / 24), 2)
  // 
  time.hour = PrefixInteger(Math.floor((second / 3600) % 24), 2)
  // 
  time.minute = PrefixInteger(Math.floor((second / 60) % 60), 2)
  // 
  time.second = PrefixInteger(Math.floor(second % 60), 2)

  var newtime = ''
  if (time.day > 0) {
    newtime =
      time.day +
      '' +
      time.hour +
      '' +
      time.minute +
      '' +
      time.second +
      ''
  } else {
    if (time.hour != 0) {
      newtime = time.hour + '' + time.minute + '' + time.second + ''
    } else {
      newtime = time.minute + '' + time.second + ''
    }
  }
  return newtime
}

function timeToDateObj(micro_second) {
  var time = {}
  // 
  var second = Math.floor(micro_second)
  // 
  time.day = Math.floor(second / 3600 / 24)
  // 
  time.hour = Math.floor((second / 3600) % 24)
  // 
  time.minute = Math.floor((second / 60) % 60)
  // 
  time.second = Math.floor(second % 60)

  return time

}

//
function formatMoney(number, places, symbol, thousand, decimal) {
  number = number || 0
  places = !isNaN((places = Math.abs(places))) ? places : 2
  symbol = symbol !== undefined ? symbol : ''
  thousand = thousand || ','
  decimal = decimal || '.'
  var negative = number < 0 ? '-' : '',
    i = parseInt((number = Math.abs(+number || 0).toFixed(places)), 10) + '',
    j = (j = i.length) > 3 ? j % 3 : 0
  return (
    symbol +
    negative +
    (j ? i.substr(0, j) + thousand : '') +
    i.substr(j).replace(/(\d{3})(?=\d)/g, '$1' + thousand) +
    (places
      ? decimal +
        Math.abs(number - i)
          .toFixed(places)
          .slice(2)
      : '')
  )
}
function throttle(fn, context, delay) {
  clearTimeout(fn.timeoutId)
  fn.timeoutId = setTimeout(function() {
    fn.call(context)
  }, delay)
}

// 11:03 25:19 1s
function dateformat(micro_second) {
  var time = {}
  // 
  var second = Math.floor(micro_second / 1000) // 
  time.day = PrefixInteger(Math.floor(second / 3600 / 24), 2) // 
  time.hour = PrefixInteger(Math.floor((second / 3600) % 24), 2) // 
  time.minute = PrefixInteger(Math.floor((second / 60) % 60), 2) // 
  time.second = PrefixInteger(Math.floor(second % 60), 2)
  return time
}

//0
function PrefixInteger(num, length) {
  return (Array(length).join('0') + num).slice(-length)
}

//
function isPhoneNumber(str) {
  var myreg = /^[1][3,4,5,6,7,8,9][0-9]{9}$/
  if (!myreg.test(str)) {
    return false
  } else {
    return true
  }
}

/**
 *
 * url
 *
 */
function builderUrlParams(url, data) {
  if (typeof url == 'undefined' || url == null || url == '') {
    return ''
  }
  if (typeof data == 'undefined' || data == null || typeof data != 'object') {
    return ''
  }
  url += url.indexOf('?') != -1 ? '' : '?'
  for (var k in data) {
    url += (url.indexOf('=') != -1 ? '&' : '') + k + '=' + encodeURI(data[k])
  }
  return url
}

/**
 * 
 * @param {Object} arr 
 * @param {Object} value 
 */
function isInArray(arr, value) {
  for (var i = 0; i < arr.length; i++) {
    if (value === arr[i]) {
      return true
    }
  }
  return false
}
