export const jumpBackPage = {
  methods: {
    handleBack() {
      let redirect = this.$store.state.redirectPage
      this.$store.commit({
        type: 'redirect',
        page: ''
      })
      let switchTabs = ['/pages/index/index', '/pages/member/index/index']
      if (switchTabs.indexOf(redirect) > -1) {
        uni.switchTab({
          url: redirect
        })
      } else if (redirect) {
        uni.redirectTo({
          url: redirect
        })
      } else {
        uni.switchTab({
          url: '/pages/index/index'
        })
      }
    }
  }
}

